body {
  max-width:500px;
  margin:0 auto;
  line-height:1.5em
}

a{
  color:#fff5ee;
  background:#000;
  text-decoration:none;
  font-weight:700
}

h5{
  font-weight:500
}

html{
  font:22px monospace;
  padding:5%;
  background: linear-gradient(90deg, #FEAC5E 0%, #C779D0 50%, #4BC0C8 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
